<template>
    <BaseModal
        name="artist"
        :mobileMode="true"
        :closeOutside="true"
        :isLoading="data.loading"
    >
        <template v-slot:title>
            <span>{{ data.item?.name }}</span>
        </template>
        <template v-slot:content>
            <div class="box" v-if="data.item">
                <div class="gallery">
                    <Img
                        v-if="data.item?.profileImage"
                        :preview="data.item?.profileImage?.crop"
                        :original="data.item?.profileImage?.original"
                        :alt="data.item.name"
                        aspectRatio="1 / 1"
                        :skeleton="true"
                    />

                    <div class="buttons">
                        <Button icon="tg-stars" class="secondary size-m"
                            :name="t('common.support')"
                            @click="donate"
                        />
                    </div>
                </div>
                <div class="description" v-if="data.item.description">
                    {{ data.item.description }}
                </div>


                <div class="links" v-if="data.item.links?.length">
                    <TitleWithButtons
                        :title="t('modals.artist.official-pages')"
                    />

                    <ul>
                        <li v-for="item in data.item.links" :key="item.code" @click="openLink(item)">
                            <Icon :icon="item.code && knownSites.get(item.code) ? item.code : 'link'" />

                            <div>
                                <span v-if="item.code && knownSites.get(item.code)"
                                    class="name"
                                >
                                    {{ t(`known-sites.${item.code}`) }}
                                </span>
                                <span v-else class="name" >
                                    {{ getDomainCode(item.url) }}
                                </span>

                                <span class="url">{{ item.url }}</span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
        </template>
    </BaseModal>
</template>

<script setup>
import { onMounted, reactive } from 'vue';
import { useI18n } from 'vue-i18n';

// import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
import Button from 'ðŸ”—/components/ui/Button.vue';
import Img from 'ðŸ”—/components/ui/Img.vue';
import BaseModal from 'ðŸ”—/components/modals/BaseModal.vue';
import stores from '@/stores';
import TitleWithButtons from '@/components/ui/TitleWithButtons.vue';

// import { mapMutations, mapState } from '@/map-state';
import api from 'ðŸ”—/api';
import { knownSites, copyText, message, getDomainCode } from 'ðŸ”—/utils/utils';

const { t } = useI18n();

const tg = stores.tg();
const modals = stores.modals();

const data = reactive({
    loading: true
});

const donate = () => {
    message.info(t('common.soon'));
}

const close = () => {
    modals.close('artist');
};

const copy = () => {
    // if (!user?.profile?.id) {
    //     return;
    // }

    // copyText(`https://t.me/bilboMusicBot/app?startapp=ref_${user?.profile?.id}`);
    // message.success(t('modals.invite.copied'));
};

const share = () => {
    // if (window.Telegram?.WebApp) {
    //     const external_url = `https://telegram.me/share/url?url=https://t.me/bilboMusicBot/app?startapp=ref_${
    //         user?.profile?.id
    //     }&text=${t('modals.invite.message')}`;


    //     tg.openLink({
    //         external_url
    //     });
    // }

    // close();
};

const openLink = (item) => {
    if (!item?.url) {
        return
    }

    if (item.code === 'telegram') {
        tg.openLink({
            external_url: item.url,
            type: 'telegram'
        });
    } else {
        tg.openLink({
            external_url: item.url
        });
    }
}

onMounted(async () => {
    const payload = modals.getData('artist');

    if (!payload?.code) {
        close();
        return;
    }

    data.item = await api.artists().get(payload.code);
    data.loading = false;
    console.log(data.item)
});

</script>

<style lang="scss" scoped>
    .box {
        display: flex;
        flex-direction: column;
        gap: var(--block-gap);
        padding: 20px 0;

        .gallery {
            position: relative;
            .buttons {
                position: absolute;
                right: 16px;
                bottom: 16px;
                display: flex;
                flex-direction: row;
                gap: 8px;
            }
        }

        .description {
            font: var(--font-body-2);
            color: var(--dark-500);
            white-space: pre-wrap;
        }

        .links {
            display: flex;
            flex-direction: column;
            gap: var(--block-gap);

            ul {
                list-style: none;
                margin: 0;
                padding: 0;
                display: flex;
                flex-direction: column;
                li {
                    cursor: pointer;
                    display: flex;
                    flex-direction: row;
                    gap: 12px;
                    align-items: center;
                    padding: 8px 0;
                    width: 100%;

                    svg {
                        width: 38px;
                        height: 38px;
                        fill: var(--dark-500);
                        flex-shrink: 0;
                    }

                    div {
                        display: flex;
                        flex-direction: column;
                        gap: 4px;

                        .name {
                            font: var(--font-body-1);
                            color: var(--dark-500);
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            &::first-letter {
                                text-transform: uppercase;
                            }
                        }

                        .url {
                            font: var(--font-caption);
                            color: var(--gray-500);
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            direction: rtl;
                            text-align: left;
                            max-width: 200px;
                        }
                    }
                }
            }
        }
    }
</style>
